export default {
	 mounted() {
        const canvas = document.getElementById("canvas");
        const ctx = canvas.getContext("2d");
        const rndCl = () => Math.floor(Math.random() * 225);
        const width = window.innerWidth;
        const height = window.innerHeight;
        let base_list = [];
        canvas.width = width;
        canvas.height = height;

        // 绘制园
        const drawRounds = (obj, index) => {
            let { x, y, r, color } = obj;
            ctx.beginPath();
            ctx.arc(x, y, r, 0, 2 * Math.PI);
            if (this.isColor) {
                ctx.fillStyle = color;
            } else {
                ctx.fillStyle = this.roundColor
            }
            ctx.fill();
            ctx.closePath();
        }

        //判断移动方向
        const controlDirection = (obj) => {
            if (obj.x >= (width - obj.r)) {
                obj.controlX = "left";
            } else if (obj.x <= parseInt(obj.r / 2)) {
                obj.controlX = "right";
            }

            if (obj.y >= (height - obj.r)) {
                obj.controlY = "bottom";
            } else if (obj.y <= parseInt(obj.r / 2)) {
                obj.controlY = "top"
            }
            return obj
        }

        //划线
        const drawLine = (list) => {
            list.map((item, index) => {
                ctx.beginPath();
                ctx.moveTo(item.x1, item.y1);
                ctx.lineTo(item.x2, item.y2);
                ctx.LineWeight = 1;
                if (this.isColor) {
                    ctx.strokeStyle = item.color;
                } else {
                    ctx.strokeStyle = this.lineColor
                }
                ctx.stroke();
                ctx.closePath();
            })
        }

        //绘制
        const draw = (list) => {
            let dots_arr = [];
            let line_arr = [];

            list.map((item, index) => {
                let xy = controlDirection(item);
                let obj = roundMove(xy);
                dots_arr.push(obj);
            });
            dots_arr.map((item1, index1) => {
                dots_arr.map((item2, index2) => {
                    if (item1 !== item2) {
                        let x = item1.x - item2.x;
                        let y = item1.y - item2.y;
                        if (Math.abs(x) < 150 && Math.abs(y) < 150) {
                            let obj = {
                                x1: item1.x,
                                y1: item1.y,
                                x2: item2.x,
                                y2: item2.y,
                                color: item1.color
                            }
                            line_arr.push(obj)
                        }
                    }
                })
            })
            drawLine(line_arr);

            dots_arr.map((item, index) => {
                drawRounds(item, index)
            })

            base_list = dots_arr;

            setTimeout(() => {
                // if(this.paused){
                reDraw()
                // }
            }, 50)
        }

        const reDraw = () => {
            ctx.clearRect(0, 0, width, height);
            draw(base_list)
        }

        //移动
        const roundMove = (obj) => {
            switch (obj.controlX) {
                case "right":
                    obj.x++;
                    break;
                case "left":
                    obj.x--;
                    break;
                default:
            }
            switch (obj.controlY) {
                case "top":
                    obj.y++;
                    break;
                case "bottom":
                    obj.y--;
                    break;
                default:
            }
            return obj
        }

        //创造圆点
        const creatDots = () => {
            let arr = [];
            for (let i = 0; i < this.dotsNum; i++) {
                let obj = {};
                obj.x = parseInt(Math.random() * width);
                obj.y = parseInt(Math.random() * height);
                obj.r = parseInt(Math.random() * 10);
                obj.controlX = parseInt(Math.random() * 10) > 5 ? "left" : "right"
                obj.controlY = parseInt(Math.random() * 10) > 5 ? "bottom" : "top"

                obj.color = `rgba(${rndCl()},${rndCl()},${rndCl()},.3)`

                arr.push(obj)
            }
            return arr
        }

        draw(creatDots())

        //鼠标移动
        const moveXY = (event) => {
            let obj = {};
            obj.x = event.clientX;
            obj.y = event.clientY;
            obj.r = 0;
            base_list[0] = obj;
            base_list[0]["r"] = 1;
        }

        //鼠标点击
        const addXY = (event) => {
            let obj = {};
            obj.x = event.clientX;
            obj.y = event.clientY;
            obj.r = parseInt(Math.random() * 10);
            obj.color = `rgba(${rndCl()},${rndCl()},${rndCl()},.3)`;
            obj.controlX = parseInt(Math.random() * 10) > 5 ? 'left' : 'right'
            obj.controlY = parseInt(Math.random() * 10) > 5 ? 'bottom' : 'top'
            base_list.push(obj);
        }

        window.addEventListener("mousemove", moveXY);
        window.addEventListener("click", addXY)
    },
    data(){
	    let userNameChangeToSubmitButton=(rule,value,callback)=>{
	     	this.ruleForm.userName=value;
	      	if(this.ruleForm.userName!=""&&this.ruleForm.pwd!=""){
	      		this.ruleForm.handleSubmitButtonColor="success"
	      	}else{
	      		this.ruleForm.handleSubmitButtonColor="info"
	      	}
	  		if(!value){
	      		return callback(new Error('请输入账号'))
	      	}else{
	      	callback();	
	      	/*const reg=/^[A-Za-z0-9]{6,20}$/;
	      		
	      		if(reg.test(value)){
	      			callback();
	      		}else{
	      			return callback(new Error('密码至少6位,可由大小写字母和数字组成'));
	      		}*/
	      	} 
	    }   
        let pwdChangeToSubmitButton=(rule,value,callback)=>{
      	this.ruleForm.pwd=value;
      	if(this.ruleForm.userName!=""&&this.ruleForm.pwd!=""){
      		this.ruleForm.handleSubmitButtonColor="success"
      	}else{
      		this.ruleForm.handleSubmitButtonColor="info"
      	}
  		if(!value){
      		return callback(new Error('请输入密码'))
      	}else{
      	callback();	
      	/*const reg=/^[A-Za-z0-9]{6,20}$/;
      		
      		if(reg.test(value)){
      			callback();
      		}else{
      			return callback(new Error('密码至少6位,可由大小写字母和数字组成'));
      		}*/
      	} 	
      }
      return{     	
      	visible: true,
      	loading: false,
      	scalezoominClass:'',
      	scalezoomoutClass:'',
      	ruleForm:{
      		 userName:'',
		     pwd:'',
		     checked:false,
		     handleSubmitButtonColor:'info'
      	},
      	rules:{
      		userName:[
      		{
      			trigger:['blur','change'],
      			validator:userNameChangeToSubmitButton,
      		}
      		],
      		pwd:[
      		{
      			validator:pwdChangeToSubmitButton,
      			trigger:['blur','change']
      		}
      		]
      	}
      }
    },
    props: {
        dotsNum: {
            type: Number,
            default: 50
        },
        isColor: {
            type: Boolean,
            default: true
        },
        roundColor: {
            type: String,
            default: "#999"
        },
        lineColor: {
            type: String,
            default: "#ccc"
        }
    },
    created() {
    	if(localStorage.userName!=null){
    		this.ruleForm.userName=localStorage.userName
    		this.ruleForm.pwd=localStorage.pwd
    	}
    	if(this.ruleForm.userName!=""&&this.ruleForm.pwd!=""){
      		this.ruleForm.handleSubmitButtonColor="success"
      	}else{
      		this.ruleForm.handleSubmitButtonColor="info"
      	}
      		this.scalezoominClass="scalezoominClass";
    },
    methods:{
	 	changePass(value) {
        	this.visible = !(value === 'show');
      	},    //判断渲染，true:暗文显示，false:明文显示
		Login(){
			 this.$router.push({ path: '/Main' });
			/*this.$refs.ruleForm.validate((valid)=>{
				if(valid){
				       this.loading = true;
					   this.$api.login('authentication/login',  {"username": this.ruleForm.userName, "password": this.ruleForm.pwd,"grant_type":'password'}, success => {
					   this.$message({
					        message: '登录成功',
					        type: 'success'
			         	});
			         	this.scalezoomoutClass="scalezoomoutClass"
			         	localStorage.token=success.obj;
			         	if(this.ruleForm.checked){
					 	   localStorage.userName=this.ruleForm.userName;
					 	   localStorage.pwd=this.ruleForm.pwd;
					 	}
					   this.$router.push({ path: '/Main' });
					  },
					 failure=>{
					   this.loading = false;
					   this.$message({
				          message: '用户名或密码有误',
				          duration:'1000',
				          type:'error'
			         	});
					 })
				}else{
					
				}
			})*/
		}
	   }
  }


