<template>
	<div  class="bgClass">
		<canvas id="canvas">
    	</canvas>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="container containerInputColor"  v-loading="loading" background="#cccccc" :class="[scalezoominClass,scalezoomoutClass]">
			<i class="el-icon-circle-close-outline"></i>
			<img src="../../static/images/timg.png"/>
			<el-form-item prop="userName">
		    	<el-input type="text"   v-model="ruleForm.userName"  placeholder="账号"  title="账号" clearable autocomplete="off" v-on:keyup.13.native.prevent="Login" autofocus></el-input>
		    </el-form-item>
		    <el-form-item v-if="visible" prop="pwd">
		    	<el-input type="password"   v-model="ruleForm.pwd" placeholder="密码"  title="密码" autocomplete="off" v-on:keyup.13.native.prevent="Login" >
		    		<i slot="suffix"  @click="changePass('show')" style="cursor:pointer;"
	               	class=" iconfont icon-yanjing_xianshi_o" title="显示密码" ></i>
		    	</el-input>
		    </el-form-item>
		    <el-form-item v-else  prop="pwd">
		    	<el-input type="text"   v-model="ruleForm.pwd" placeholder="密码"  title="密码" autocomplete="off" v-on:keyup.13.native.prevent="Login" >
		    		<i slot="suffix" @click="changePass('hide')" style="cursor:pointer;"
	               	class=" iconfont icon-yanjing_yincang_o" title="隐藏密码" ></i>
		    	</el-input>
		    </el-form-item>
		    <el-form-item>
		    	<el-checkbox v-model="ruleForm.checked"  class="remember" >记住我（请在私人计算机上使用此功能）</el-checkbox>
		    	<a class="forgetPassword">忘记登录？</a>
		    </el-form-item>
		    <el-form-item  style="width:100%;">
		   		<el-button v-if="ruleForm.handleSubmitButtonColor=='info'" type="info"  style="width:100%;" @click.native.prevent="Login">登陆</el-button>	   		
		   		<el-button v-if="ruleForm.handleSubmitButtonColor=='success'"  type="success" style="width:100%;"  @click.native.prevent="Login"  :loading="loading">登陆</el-button>	   	
		   	</el-form-item>
		   	 <el-form-item  style="width:100%;">
		   		<el-button type="info"  style="width:100%;" @click.native.prevent="Reigster">注册</el-button>	   	
		   	</el-form-item>
	   </el-form>
	</div>   
</template>
<script>
  import Login from '../assets/js/Login.js'
  export default Login
</script>
<style scoped lang="scss" >
@import '../assets/css/Login.scss'
</style>
<style>
   .containerInputColor  .el-input__inner {
        background: #000000!important;
        color: #ffffff!important;
    }
</style>


